export const arrOfObjSort = async (arr, key, asc=true) => {

  //CHECK IF THIS KEY IS NUMERIC OR A STRING. ALSO LOOK FOR UNDEFINED
  let Numeric = true
  for (let Each of arr){
    if (typeof Each[key] != "undefined"){
      if ( isNaN(Each[key]) ){
        Numeric = false
        break
      }
    }
  }

  if (Numeric && asc) {
    arr.sort((a, b) => {
      if (typeof a[key] == "undefined"){
        return -1;
      }
      if (typeof b[key] == "undefined"){
        return 1;
      }
      return a[key] - b[key]
    })
  }

  else if (Numeric && !asc) {
    arr.sort((a, b) => {
      if (typeof a[key] == "undefined"){
        return 1;
      }
      if (typeof b[key] == "undefined"){
        return -1;
      }
      return b[key] - a[key]
    })
  }


  else if (asc) {

    arr.sort((a, b) => {
        if (typeof a[key] == "undefined"){
          return 1;
        }
        if (typeof b[key] == "undefined"){
          return -1;
        }

        let fa = a[key].toLowerCase(),
            fb = b[key].toLowerCase();

        if (fa < fb) {
            return -1;
        }
        if (fa > fb) {
            return 1;
        }
        return 0;
    })
  }

  else {
    arr.sort((a, b) => {
        if (typeof a[key] == "undefined"){
          return -1;
        }
        if (typeof b[key] == "undefined"){
          return 1;
        }

        let fa = a[key].toLowerCase(),
            fb = b[key].toLowerCase();

        if (fa > fb) {
            return -1;
        }
        if (fa < fb) {
            return 1;
        }
        return 0;
    })
  }

  return arr
}

